.job-entry {
  position: relative;
}

.job-entry>h3 {
  position: absolute;
  left: -24px;
  top: -11px;
}

.job-entry .workpanel-marker {
  margin-bottom: 20px;
}

.workpanel-marker {
  background-color: black;
  display: block;
  border-radius: .2rem;
  width: 40px;
  height: 40px;
  margin-left: 55px;
}

.workpanel-marker-kpmg {
  background: -webkit-gradient( linear, left bottom, left top, from(white), color-stop(50%, white), color-stop(50%, #3b5998), to(#3b5998));
  background: -webkit-linear-gradient( bottom, white 0%, white 50%, #3b5998 50%, #3b5998 100%);
  background: -o-linear-gradient( bottom, white 0%, white 50%, #3b5998 50%, #3b5998 100%);
  background: linear-gradient( 0deg, white 0%, white 50%, #3b5998 50%, #3b5998 100%);
}

.workpanel-marker-interset {
  background: -webkit-gradient( linear, left bottom, left top, from(#f47721), color-stop(50%, #f47721), color-stop(50%, #3B5374), to(#3B5374));
  background: -webkit-linear-gradient( bottom, #f47721 0%, #f47721 50%, #3B5374 50%, #3B5374 100%);
  background: -o-linear-gradient( bottom, #f47721 0%, #f47721 50%, #3B5374 50%, #3B5374 100%);
  background: linear-gradient( 0deg, #f47721 0%, #f47721 50%, #3B5374 50%, #3B5374 100%);
}


.workpanel-marker-helpwear {
  background: -webkit-gradient( linear, left bottom, left top, from(#FFFFFF), color-stop(50%, #FFFFFF), color-stop(50%, #3BA9DA), color-stop(50%, #3BA9DA));
  background: -webkit-linear-gradient( bottom, #FFFFFF 0%, #FFFFFF 50%, #3BA9DA 50%, #3BA9DA 50%);
  background: -o-linear-gradient( bottom, #FFFFFF 0%, #FFFFFF 50%, #3BA9DA 50%, #3BA9DA 50%);
  background: linear-gradient( 0deg, #FFFFFF 0%, #FFFFFF 50%, #3BA9DA 50%, #3BA9DA 50%);
}


.workpanel-marker-tech {
  background: -webkit-gradient( linear, left bottom, left top, from(#000000), color-stop(50%, #000000), color-stop(50%, #EAAA00), to(#EAAA00));
  background: -webkit-linear-gradient( bottom, #000000 0%, #000000 50%, #EAAA00 50%, #EAAA00 100%);
  background: -o-linear-gradient( bottom, #000000 0%, #000000 50%, #EAAA00 50%, #EAAA00 100%);
  background: linear-gradient( 0deg, #000000 0%, #000000 50%, #EAAA00 50%, #EAAA00 100%);
}

.workpanels .timeline-entry {
  background-color: #f4f4f4;
  display: block;
  border-radius: .2rem;
  left: 107px;
  padding: 10px;
  position: relative;
  top: -60px;
  width: calc(100% - 150px);
}

.timeline-entry-title>h4 {
  font-family: 'Ubuntu Mono', monospace;
  display: inline;
}


